













html {
  height: 100%;
}
body {
  min-height: 100%;
  position: relative;
  padding-bottom: 80px;
}
main {
  padding-bottom: 50px;
}
main .main-content, main .sidebar {
    padding-top: 50px;
}

.navbar {
  min-height: 80px;
}
.navbar .navbar-brand {
    font-size: 2rem;
    font-weight: bold;
}
.navbar .nav-item.active {
    border-bottom: 4px solid #70C14A;
}
.navbar-light .navbar-nav .active > .nav-link, .navbar-light .navbar-nav .nav-link.active, .navbar-light .navbar-nav .nav-link.show, .navbar-light .navbar-nav .show > .nav-link {
  color: #70C14A;
}

.categories-header {
  font-weight: bold;
}
.list-group-item {
  font-size: 1rem;
  border: none;
}
.list-group-item.active {
    background-color: #70C14A;
}
.list-group-item.active a {
      color: #fff;
}
.list-group-item.active:hover a {
      color: #000;
}
.list-group-item a {
    color: #000;
}
.list-group-item a:hover {
      color: #70C14A;
      text-decoration: none;
      border-bottom: 2px solid #70C14A;
}

.page-header {
  background-color: #333;
  color: #fff;
  height: 80px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.page-header p {
    margin: 0;
    font-size: 2rem;
}

footer {
  background-color: #333;
  color: #fff;
  padding: 30px 0;
  position: absolute;
  bottom: 0;
  width: 100%;
}
footer p {
    margin: 0;
    text-align: center;
}



































.card {
  border: none;
  margin: 1rem 0;
  padding: 30px 0;
  border-bottom: 1px solid #bbb;
}
.card .article-img {
    max-height: 400px;
    overflow: hidden;
    cursor: pointer;
}
.card .article-img img.card-img-top {
      height: 100%;
      -webkit-transition: -webkit-transform .6s ease-in-out;
      transition: -webkit-transform .6s ease-in-out;
      transition: transform .6s ease-in-out;
      transition: transform .6s ease-in-out, -webkit-transform .6s ease-in-out;
}
.card .article-img img.card-img-top:hover {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
}
.card:first-child {
    padding-top: 0;
}
.card .article-link {
    cursor: pointer;
    -webkit-transition: color .15s ease-in-out;
    transition: color .15s ease-in-out;
    display: block;
    color: #000;
    margin: 0;
}
.card .article-link:hover {
      color: #70C14A;
      text-decoration: none;
}
.card .article-link h5 {
      margin: 0;
}
.card .article-meta {
    font-size: 0.85rem;
}
.card .card-title h2 {
    font-size: 1.75rem;
}
.card .card-text {
    margin: 0;
    padding: 1rem 0;
}
.card .article-link-btn {
    background-color: #70C14A;
    color: #fff;
}
.card .article-link-btn:hover {
      background-color: #309006;
}

header h1 {
  font-size: 2.15rem;
}
header .article-date {
  color: #aaa;
  display: block;
}
.article-body .article-thumbail {
  height: 400px;
}
.article-body .article-thumbail img {
    width: 100%;
    height: 100%;
}
.article-body .article-content {
  margin: 2rem 0;
}
.article-body .article-content p {
    margin: 0;
}








































